.main-container {
    display: flex;
    
    flex: 1;
    gap: 2rem;
}

@media screen and (max-width: 1024px) {
    .main-container {
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 480px) {
    .main-container {
        display: flex;
        flex-direction: column;
    }
}